@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
}

:root {
  --lm-bg: #f6f8ff;
  --lm-bg-content: #fefefe;
  --lm-text: #4b6a9b;
  --lm-text-alt: #2b3442;
  --lm-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.2);
  --lm-shadow-inactive: 0px 16px 30px -10px rgba(0, 0, 0, 0.2);
  --lm-icon-bg: brightness(100%);
  /* Btn */
  --btn: #0079ff;
  --btn-hover: #60abff;
}

body {
  background-color: var(--lm-bg);
  /* font-size: 13px; */
  color: var(--lm-text);
  /* width: 100%; */
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}

.nav-items > ul {
  display: flex;
  /* padding: 0px 20px; */
  list-style: none;
}

.nav-items > ul > li {
  padding: 10px;
  margin: 10px;
}
.search {
  padding: 10px;
}

.res-logo {
  width: 100%;
  height: 50%;
}

.res-card {
  scale: 1;
  transition: all 90ms ease-in-out;
}

.res-card:hover {
  scale: 95%;
}

.filter-btn {
  margin: 10px;
  padding: 3px;
  cursor: pointer;
}

.shimmer-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 60px;
}
/* ShimmerVideoCard.css */
.shimmer-video-card {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.shimmer-thumbnail {
  width: 250px;
  height: 150px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite linear;
}

.shimmer-details {
  margin-left: 10px;
}

.shimmer-title {
  width: 40%;
  height: 18px;
  margin-bottom: 10px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite linear;
}

.shimmer-description {
  width: 90%;
  height: 15px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite linear;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

.login {
  cursor: pointer;
  padding: 0 10px;
}

.search-btn {
  cursor: pointer;
  margin: 0 10px;
  padding: 0 10px;
}

.search-box {
  margin: 0 10px;
  padding: 4px 10px;
}

.filter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.circle1 {
  position: fixed;
  display: flex;
  content: "";
  top: 15%;
  left: 30%;
  border-radius: 50%;
  box-shadow: 0 0 50px #d84869;
  background: linear-gradient(-30deg, #5648d8, #8641f4);
  animation: circle1Animate 9.5s infinite;
}

@keyframes circle1Animate {
  50% {
    left: 33%;
    top: 14%;
    scale: 1.11;
  }
}

.circle2 {
  position: absolute;
  bottom: 17%;
  right: 30%;
  border-radius: 50%;
  box-shadow: 0 0 50px #5648d8;
  background: linear-gradient(-30deg, #d84869, #f46641);
  animation: circle2Animate 5s infinite;
}
@keyframes circle2Animate {
  50% {
    bottom: 20%;
    right: 33%;
    scale: 1.11;
  }
}

.active {
  opacity: 1;
  transform: scale(1);
}

.btn {
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 20px;
  font-family: cursive;
  font-size: medium;
  font-weight: 520;
}

.btn:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(4px);
  width: calc(100% + 5px);
  height: calc(100% + 5px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 20px;
}

.btn:active {
  color: #000;
}

.btn:active:after {
  background: transparent;
}

.btn:hover:before {
  opacity: 1;
}

.btn:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 20px;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@media only screen and (max-width: 640px) {
  .custom {
    width: 70%;
  }

  .custom-resCard-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
  }
}
