:root {
  --lm-bg: #f6f8ff;
  --lm-bg-content: #fefefe;
  --lm-text: #4b6a9b;
  --lm-text-alt: #2b3442;
  --lm-shadow: 0px 16px 30px -10px #4660bb33;
  --lm-shadow-inactive: 0px 16px 30px -10px #0003;
  --lm-icon-bg: brightness(100%);
  --btn: #0079ff;
  --btn-hover: #60abff;
}

body {
  background-color: var(--lm-bg);
  color: var(--lm-text);
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

.nav-items > ul {
  list-style: none;
  display: flex;
}

.nav-items > ul > li {
  margin: 10px;
  padding: 10px;
}

.search {
  padding: 10px;
}

.res-logo {
  width: 100%;
  height: 50%;
}

.res-card {
  transition: all 90ms ease-in-out;
  scale: 1;
}

.res-card:hover {
  scale: 95%;
}

.filter-btn {
  cursor: pointer;
  margin: 10px;
  padding: 3px;
}

.shimmer-container {
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 60px;
  display: flex;
}

.shimmer-video-card {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

.shimmer-thumbnail {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  width: 250px;
  height: 150px;
  animation: 1.5s linear infinite shimmer;
}

.shimmer-details {
  margin-left: 10px;
}

.shimmer-title {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  width: 40%;
  height: 18px;
  margin-bottom: 10px;
  animation: 1.5s linear infinite shimmer;
}

.shimmer-description {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  width: 90%;
  height: 15px;
  animation: 1.5s linear infinite shimmer;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }

  100% {
    background-position: 200% 0;
  }
}

.login {
  cursor: pointer;
  padding: 0 10px;
}

.search-btn {
  cursor: pointer;
  margin: 0 10px;
  padding: 0 10px;
}

.search-box {
  margin: 0 10px;
  padding: 4px 10px;
}

.filter {
  justify-content: center;
  align-items: center;
  display: flex;
}

.circle1 {
  content: "";
  background: linear-gradient(-30deg, #5648d8, #8641f4);
  border-radius: 50%;
  animation: 9.5s infinite circle1Animate;
  display: flex;
  position: fixed;
  top: 15%;
  left: 30%;
  box-shadow: 0 0 50px #d84869;
}

@keyframes circle1Animate {
  50% {
    top: 14%;
    left: 33%;
    scale: 1.11;
  }
}

.circle2 {
  background: linear-gradient(-30deg, #d84869, #f46641);
  border-radius: 50%;
  animation: 5s infinite circle2Animate;
  position: absolute;
  bottom: 17%;
  right: 30%;
  box-shadow: 0 0 50px #5648d8;
}

@keyframes circle2Animate {
  50% {
    bottom: 20%;
    right: 33%;
    scale: 1.11;
  }
}

.active {
  opacity: 1;
  transform: scale(1);
}

.btn {
  color: #fff;
  cursor: pointer;
  z-index: 0;
  background: #111;
  border: none;
  border-radius: 20px;
  outline: none;
  width: 220px;
  height: 50px;
  font-family: cursive;
  font-size: medium;
  font-weight: 520;
  position: relative;
}

.btn:before {
  content: "";
  z-index: -1;
  filter: blur(4px);
  opacity: 0;
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 20px;
  width: calc(100% + 5px);
  height: calc(100% + 5px);
  transition: opacity .3s ease-in-out;
  animation: 20s linear infinite glowing;
  position: absolute;
  top: -2px;
  left: -2px;
}

.btn:active {
  color: #000;
}

.btn:active:after {
  background: none;
}

.btn:hover:before {
  opacity: 1;
}

.btn:after {
  z-index: -1;
  content: "";
  background: #111;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

@media only screen and (width <= 640px) {
